
  .ros{
    width: 60%;
    margin: 1rem auto;
    display:flex;
    justify-content: space-evenly;
  
  }
  
  .player {
    width: 240px;
    height: 180px;
    margin: 50px;
    border-radius: 50%;
    text-align: center;
    display: inline-flex;
  }
  
  .weaponBtn {
    width: 100px;
    height: 50px;
    display: inline;
    margin: auto 15px;
    color: #fff;
    font-size: 18px;
    border-radius: 25px;
    background: #000;
  }
  
  .player:nth-child(2) {
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
  }
  
  .winner {
    width: 100px;
    height: 55px;
    margin: 10px auto;
    color: brown;
    font-size: 18px;
    text-align: center;
    font-weight: bold;
  }
  .player-image {
    height: 200px;
    width: auto;
  }
  button {
    width: fit-content;
    height: 50px;
    display: block;
    margin: 10px auto;
    color: #fff;
    font-size: 18px;
    border-radius: 25px;
    background: #000;
    outline: none;
  }
  
  button:hover {
    cursor: pointer;
  }

  #title{
    background-color: orange;
    color: red ;
    width: fit-content;
    font-size:x-large;
    padding: 0;
    margin-left: 32%;
  }